export default {
  namespaced: true,
  state: {
    userInfo: {},
    menuList: [{
      index: 0,
      name: '消息中心',
      icon: 'icon-message',
      router: 'message',
    },
    {
      index: 1,
      name: '账号信息',
      icon: 'icon-user-info',
      router: 'userInfo',
    },
    {
      index: 2,
      name: '下载中心',
      icon: 'icon-upload',
      router: 'downloadCenter',
    },
    {
      index: 3,
      name: '授权统计',
      icon: 'icon-tongji',
      router: 'statistical',
    },
    {
      index: 4,
      name: '日志审计',
      icon: 'icon-rizhi',
      router: 'log',
      role: ['SUPER_ADMIN']
    },
    {
      index: 5,
      name: '客户管理',
      icon: 'icon-yonghuguanli',
      router: 'userManage',
      role: ['SUPER_ADMIN', 'NAMESPACE_SUPPORTER']
    },
    {
      index: 6,
      name: '软件管理',
      icon: 'icon-ruanjianfuwu1',
      router: 'softManage',
      role: ['SUPER_ADMIN', 'NAMESPACE_SUPPORTER']
    },
    {
      index: 7,
      name: '分区管理',
      icon: 'icon-zuzhi',
      router: 'namespaceManage',
      role: ['SUPER_ADMIN', 'NAMESPACE_OWNER']
    },
    {
      index: 8,
      name: '发布管理',
      icon: 'icon-fabu',
      router: 'releaseManage',
      role: ['SUPER_ADMIN', 'NAMESPACE_OWNER', 'NAMESPACE_WRITER']
    }]
  },
  getters: {
    getMenuList: (state) => {
      return state.menuList;
    }
  },
  mutations: {
    setMenuList: (state, roleList) => {
      console.log(state.menuList);
      console.log(roleList);
      if (roleList.length === 0) {
        state.menuList = state.menuList.slice(0, 4);
        console.log(state.menuList);
      } else {
        const list = state.menuList.slice(4, 9);
        console.log(list);
        if (roleList.indexOf('SUPER_ADMIN') !== -1) {
          list.push(...state.menuList.slice(4));
        }
        if (roleList.indexOf('NAMESPACE_OWNER') !== -1) {
          list.push(...state.menuList.slice(7, 9));
        }
        if (roleList.indexOf('NAMESPACE_WRITER') !== -1) {
          list.push(...state.menuList.slice(8, 9));
        }
        if (roleList.indexOf('NAMESPACE_SUPPORTER') !== -1) {
          list.push(...state.menuList.slice(5, 7));
        }
        state.menuList = [...new Set(list)];
        console.log(state.menuList);
      }
    }
  }
};
